'use client';

import { Card } from '@/components/ui/card';
import {
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableHeader,
  TableRow,
} from '@/components/ui/table';
import { Property } from '@/types/property';
import { ScrollArea } from '@/components/ui/scroll-area';

interface PropertyDetailListProps {
  properties: Property[];
}

export default function PropertyDetailList({ properties }: PropertyDetailListProps) {
  return (
    <Card className="p-6 border border-border shadow-md">
      <h2 className="text-lg font-semibold text-foreground mb-4">
        財產詳細清單
      </h2>
      <ScrollArea className="w-full">
        <div className="overflow-x-auto">
          <Table>
            <TableHeader>
              <TableRow>
                <TableHead className="min-w-[80px]">財產別</TableHead>
                <TableHead className="min-w-[120px]">房屋持份比例<br/>(汽缸容量)</TableHead>
                <TableHead className="min-w-[100px]">房地面積<br/>(平方公尺)</TableHead>
                <TableHead className="min-w-[120px]">房地現值金額</TableHead>
                <TableHead className="min-w-[100px]">所屬年月</TableHead>
                <TableHead className="min-w-[100px]">財產所有人</TableHead>
                <TableHead className="min-w-[80px]">地目<br/>(車年)</TableHead>
                <TableHead className="min-w-[80px]">縣市別</TableHead>
                <TableHead className="min-w-[150px]">房屋座落<br/>(地段名稱/BAN名稱)</TableHead>
                <TableHead className="min-w-[80px]">持分分子</TableHead>
                <TableHead className="min-w-[80px]">持分分母</TableHead>
                <TableHead className="min-w-[80px]">信託註記</TableHead>
                <TableHead className="min-w-[100px]">登記時間</TableHead>
              </TableRow>
            </TableHeader>
            <TableBody>
              {properties.length === 0 ? (
                <TableRow>
                  <TableCell colSpan={13} className="text-center text-muted-foreground py-8">
                    尚無財產資料
                  </TableCell>
                </TableRow>
              ) : (
                properties.map((property, index) => (
                  <TableRow key={index}>
                    <TableCell>{property.propertyType || '-'}</TableCell>
                    <TableCell>{property.ownershipRatio || '-'}</TableCell>
                    <TableCell>{property.area || '-'}</TableCell>
                    <TableCell>{property.currentValue || '-'}</TableCell>
                    <TableCell>{property.period || '-'}</TableCell>
                    <TableCell>{property.owner || '-'}</TableCell>
                    <TableCell>{property.landType || '-'}</TableCell>
                    <TableCell>{property.city || '-'}</TableCell>
                    <TableCell>{property.location || '-'}</TableCell>
                    <TableCell>{property.numerator || '-'}</TableCell>
                    <TableCell>{property.denominator || '-'}</TableCell>
                    <TableCell>{property.trustNote || '-'}</TableCell>
                    <TableCell>{property.registrationTime || '-'}</TableCell>
                  </TableRow>
                ))
              )}
            </TableBody>
          </Table>
        </div>
      </ScrollArea>
    </Card>
  );
}

